body{
    margin:0;
    font-family:sans-serif;
}

.header{
    font-size: 2em;
    padding:20px;
    height:50px;
    color:white;
    background-color: royalblue;
}

.todoForm{
    width:100%;
    margin:auto;
    margin-top:20px;
}

.todoinput{
    width:100%;
    border:none;
    border-bottom: 1px solid whitesmoke;
    text-align:center;
    font-size: 1.5em;
    height:50px;
}

.todobutton{
    width:100%;
    border:none;
    height:50px ;
    background-color: tomato;
    color:white;
    font-size:1.2em;
    margin-top: 20px;

}

.todoItem{
    margin:auto;
    border:2px solid whitesmoke;
    padding: 20px;
    margin-top: 20px;
}

.todoName{

}

.deleteButton{
    width:100px;
    border:none;
    height:50px;
    background-color: tomato;
    color:white;
    border-radius:2px;
}